import Component from "jx/core/comps/Component";
/**
 * Manage a simple task in background.
 */
export default class Daemon extends Component {
    constructor(dataObject) {
        super(Object.assign({
            interval: 30000,
            autoStart: false,
            func: evt => { }
        }, dataObject));
    }
    _create() {
        super._create();
        this.intID = null;
        this._ready = true;
    }
    start() {
        this.stop();
        this.intID = setInterval(this.dataObject.func, this.dataObject.interval);
        this.dataObject.func({ target: this });
    }
    stop() {
        if (this.intID) {
            clearInterval(this.intID);
            this.intID = null;
        }
    }
}
